/* @import url('https://fonts.googleapis.com/css2?family=Nova+Square&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');

.poweredBy {
  font-size: 16px;
  font-family: 'Lato', sans-serif;
  color: white;
  font-weight: bold;

  align-self: center;
  /* font-family Nova Square ; cursive; */
}

.bottomMsg {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  flex-grow: 0;
  gap: 10px;
  width: 100%;
  height: 20px;
  margin-top: 40px;
  /* text-align: right; */
  /* bottom: 15px; */
}

.logoContainer {
  display: flex;
  position: relative;
  flex-grow: 0;
  justify-content: center;
  height: 95px;
  width: 121px;
  margin-top: 15px;
  margin-bottom: 20px;
}

.loginContainer {
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(42, 123, 183, 0.2);
  border-radius: 16px;
  width: 340px;
  flex-grow: 0;
  background-color: white;
  z-index: 1;
  padding-top: 10px;
  padding-bottom: 10px;
}

.loginBackground {
  background: linear-gradient(
    180deg,
    #ffffff 0%,
    rgb(226, 226, 226) 20%,
    rgb(126, 188, 219) 100%
  );
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100vh;
  z-index: -1;
}

.centeredFlex {
  align-items: center;
  display: flex;
  border-radius: 10px;
  width: 295px;
}

.boxLoginPassword {
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
}

.loginBtnWraper {
  margin-top: 29px;
  display: flex;
  flex-direction: column;
  gap: 22px;
  /* background: #ffffff 0% 0% no-repeat padding-box; */
}

.loginWrapper {
  flex-direction: column;
  gap: 22px;
  max-width: fit-content;
}

.blueButton {
  background: transparent
    linear-gradient(180deg, rgba(0, 166, 246, 1) 0%, rgba(37, 184, 254, 1) 100%)
    0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px rgba(28, 179, 252, 1);
  border-radius: 10px;
  width: 295px;
  height: 43px;
}

h2 {
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  font-family: 'Lato', sans-serif;
  letter-spacing: 0px;
  color: rgba(6, 6, 6, 1);
  margin: 0;
  margin-bottom: 30px;
  margin-top: 10px;
}

.forgotPasswordBox {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  gap: 3px;
  font-size: 13px;
  font-family: 'Lato', sans-serif;
}

.forgotPasswordBox > a {
  text-decoration: none;
  font-size: 16px;
  font-family: 'Lato', sans-serif;
}

.bubble {
  height: 21px;
  width: 21px;
  border-radius: 50%;
  box-shadow: 0px 0px 4px 10px rgba(254, 254, 254, 0.2);
  background: rgba(255, 255, 255, 0.2);
  filter: saturate(4);
  position: absolute;
  z-index: 0;
}
.bubbles div:nth-child(1) {
  right: 10vw;
  animation: bubbleUp 30s linear infinite;
}
.bubbles div:nth-child(2) {
  right: 16vw;
  animation: bubbleUp 24s linear infinite;
}
.bubbles div:nth-child(3) {
  right: 34vw;
  animation: bubbleUp 32s linear infinite;
}
.bubbles div:nth-child(4) {
  right: 42vw;
  animation: bubbleUp 27s linear infinite;
}
.bubbles div:nth-child(5) {
  right: 52vw;
  animation: bubbleUp 25s linear infinite;
}
.bubbles div:nth-child(6) {
  right: 68vw;
  animation: bubbleUp 40s linear infinite;
}
.bubbles div:nth-child(7) {
  right: 74vw;
  animation: bubbleUp 33s linear infinite;
}
.bubbles div:nth-child(8) {
  right: 84vw;
  animation: bubbleUp 42s linear infinite;
}
.bubbles div:nth-child(9) {
  right: 90vw;
  animation: bubbleUp 26s linear infinite;
}
.bubbles div:nth-child(10) {
  right: 96vw;
  animation: bubbleUp 20s linear infinite;
}

@keyframes bubbleUp {
  0% {
    transform: scale(1.5) translateY(80vh);
  }
  25% {
    transform: scale(1.4) translateY(70vh);
  }
  50% {
    transform: scale(1.3) translateY(60vh);
  }
  75% {
    transform: scale(1.2) translateY(40vh);
  }
  100% {
    transform: scale(1) translateY(0vh);
  }
}

@media (min-height: 900px) {
  .bottomMsg {
    margin-top: 220px;
    height: 40px;
  }
  .logoContainer {
    margin-top: 115px;
    margin-bottom: 79px;
  }
}

@media (min-height: 768px) {
  .logoContainer {
    height: 151px;
    width: 192px;
    margin-top: 70px;
    margin-bottom: 40px;
  }
  .bottomMsg {
    margin-top: 70px;
    height: 30px;
  }
}
