@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');

.dashboardContainer {
  width: 100%;
}

/* Inicio do Mobile */

.routerOutlet {
  width: 100%;
  height: 100vh;
  margin-top: 70px;
  background-color: #f3f6f9;
  overflow: scroll;
}

.mobileNavContainer {
  width: 100%;
  position: fixed;
  top: 0;
  height: 70px;
  background: transparent
    linear-gradient(180deg, #3375a7 0%, #2e6a98 45%, #1a3b54 100%) 0% 0%
    no-repeat padding-box;
  display: flex;
  justify-content: space-between;
  z-index: 9999;
}

.mobileMenuBtnWrapper {
  width: 50px;
  height: 70px;
}

.mobileMenuBtnWrapper > div {
  border: 1px solid white;
  position: absolute;
  left: 10px;
  transition: all 300ms ease-in-out;
}

.mobileMenuBtnWrapper > div:nth-child(1) {
  top: 26px;
  width: 20px;
}

.mobileMenuBtnWrapper.openedMenu > div:nth-child(1) {
  transform: rotateZ(45deg) translateY(2px) translateX(9px);
  border: 2px solid white;
}

.mobileMenuBtnWrapper > div:nth-child(2) {
  top: 34px;
  width: 14px;
}

.mobileMenuBtnWrapper.openedMenu > div:nth-child(2) {
  transform: translateX(-15px);
  opacity: 0;
}

.mobileMenuBtnWrapper > div:nth-child(3) {
  top: 42px;
  width: 20px;
}

.mobileMenuBtnWrapper.openedMenu > div:nth-child(3) {
  transform: rotateZ(-45deg) translateY(-2px) translateX(9px);
  border: 2px solid white;
}

.mobileMenuBtnWrapper > div:nth-child(1),
.mobileMenuBtnWrapper > div:nth-child(3) {
  width: 20px;
}

.mobileLogoWrapper {
  width: 100px;
  display: flex;
  align-items: center;
  height: 70px;
}

.mobileLogoWrapper > img {
  width: 97px;
  height: 28px;
}

.mobileDropdown {
  top: -100vh;
  width: 100%;
  height: calc(100vh - 70px);
  position: absolute;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  overflow-y: scroll;
  opacity: 0;
  transition: all 500ms ease-in-out;
  z-index: 9998;
  /* background-color: #1a3b54; */
  background: transparent
    linear-gradient(0deg, #3375a7 0%, #2e6a98 5%, #1a3b54 100%) 0% 0% no-repeat
    padding-box;
}

.mobileDropdown .menuDivider {
  width: 40%;
}

.mobileDropdown.openedMenu {
  top: 70px;
  opacity: 1;
}

.mobileDropdown ul {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.mobileDropdown li {
  height: 15vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in-out;
  gap: 30px;
}

.mobileDropdown a {
  height: 15vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in-out;
  gap: 30px;
  text-decoration: none;
}

.mobileDropdown li:hover {
  transform: scale(1.1);
}

.mobileDropdown li > img {
  height: 30px;
  width: 30px;
  filter: drop-shadow(0px 3px 6px #000000);
}

.mobileDropdown li a > img {
  height: 30px;
  width: 30px;
  filter: drop-shadow(0px 3px 6px #000000);
}

.mobileDropdown li > img {
  height: 30px;
  width: 30px;
  filter: drop-shadow(0px 3px 6px #000000);
}

.mobileDropdown li a > img {
  height: 30px;
  width: 30px;
  filter: drop-shadow(0px 3px 6px #000000);
}

.mobileDropdown li > p {
  color: white;
  font-size: 20px;
  text-shadow: 0px 3px 6px #000000;
}

.mobileDropdown li > a > p {
  color: white;
  font-size: 20px;
  text-shadow: 0px 3px 6px #000000;
}

.mobileDropdown a.activePath p {
  text-decoration: none;
  background-color: #3375a7;
}

.mobileDropdown a.activePath {
  background-color: #3375a7;
}

/* 0,78 */

@media (min-width: 768px) {
  .routerOutlet {
    width: calc(100% - 90px);
    margin-top: 0px;
    margin-left: 90px;
    background-color: #f3f6f9;
  }
}

/* Desktop */
.navContainer {
  position: fixed;
  background: transparent
    linear-gradient(180deg, #3375a7 0%, #2e6a98 45%, #1a3b54 100%) 0% 0%
    no-repeat padding-box;
  box-shadow: 0px 3px 6px #000000;
  height: 100vh;
  width: 90px;
  transition: all 300ms ease-in-out;
  overflow: hidden;
}

.navContainer.openedMenu,
.navContainer:hover {
  width: 240px;
}

.navHead {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.menuBtnWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  /* transition: all .2s ease-in-out; */
}

/* .menuBtnWrapper:hover {
  /* transform: scale(1.25);  */
/* filter: invert(); */
/* } */

.logoWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px;
  transition: inherit;
}

.logoWrapper.openedMenu,
.navContainer:hover .logoWrapper {
  height: 105px;
}

.logoWrapper > img:first-child {
  transition: 300ms ease-out;
  height: 30px;
}

.logoWrapper.openedMenu > img:first-child,
.navContainer:hover .logoWrapper > img:first-child {
  opacity: 0;
}

.logoWrapper > img:last-child {
  opacity: 0;
  margin-top: -30px;
  width: 108px;
  height: 30px;
}

.logoWrapper.openedMenu > img:last-child,
.navContainer:hover .logoWrapper > img:last-child {
  height: 50px;
  width: 180px;
  opacity: 1;
  transition: opacity 600ms cubic-bezier(1, 0, 0, 0);
}

.menuWrapper ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  list-style: none;
}

.menuWrapper li {
  width: 100%;
  transition: all 0.3s ease-in-out;
}

.menuWrapper a {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}

.menuWrapper a:visited {
  text-decoration: none;
}

.menuWrapper.openedMenu a,
.navContainer:hover .menuWrapper a {
  justify-content: flex-start;
  width: 100%;
  gap: 40px;
}

.menuWrapper p {
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  font-family: 'Lato', sans-serif;
  color: #ffffff;
  text-decoration: none;
  display: none;
}

.menuWrapper a.activePath p {
  text-decoration: none;
}

.menuWrapper a.activePath {
  background-color:  #2fa3fb;
}

.menuWrapper li:hover {
  /* filter: invert(); */
  transform: scale(1.1);
  background-color: #2fa3fb;
}

.menuWrapper a > img {
  height: 30px;
  width: 30px;
}

.menuWrapper.openedMenu img,
.navContainer:hover .menuWrapper img {
  margin-left: 22px;
}

.menuWrapper.openedMenu p,
.navContainer:hover .menuWrapper p {
  display: inline;
}

.menuBtnWrapper > img {
  height: 30px;
  width: 30px;
}

/* .menuBtnWrapper {
  gap: 60px;
  justify-content: space-around;
} */

.logoAtlas {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px;
  flex-grow: 1;
  transition: inherit;
}

.logoAtlas > img {
  height: 30px;
  position: absolute;
  bottom: 45px;
}

.logoAtlas.openedMenu > img:first-child,
.navContainer:hover .logoAtlas > img:first-child {
  opacity: 0;
}

.logoAtlas > img:last-child {
  opacity: 0;
}

.logoAtlas.openedMenu > img:last-child,
.navContainer:hover .logoAtlas > img:last-child {
  opacity: 1;
  transition: opacity 600ms cubic-bezier(1, 0, 0, 0);
}

.menuDivider {
  width: 45px;
  border: 1px solid #535353;
  opacity: 0.5;
  transition: all 500ms ease-in-out;
}

.menuDivider.openedMenu {
  width: 55px;
}

/* User Bar Alignment */

.userBar {
  display: flex;
  justify-content: flex-end;
  color: white;
  background-color: #f3f6f9;
  gap: 45px;
}

.userBar > div {
  height: 60px;
  display: flex;
  align-items: center;
}

.userBar > div:last-child {
  margin-right: 60px;
}
/* userBar mobile config */
.userBar.mobile {
  gap: 20px;
  background: transparent;
}

.userBar.mobile > div {
  height: 70px;
}

.userBar.mobile > div:last-child {
  margin-right: 15px;
  height: 70px;
}

.separatedMobileCalendar {
  background: transparent;
}
.separatedMobileCalendar > div {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;

}
