@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap');

.cardWrapper {
  display: flex;
  width: 40%;
  max-width: 350px;
  max-height: 180px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 13px;
  margin-top: 10px;
}

.cardWrapper > h3 {
  text-align: center;
  margin: 0;
  text-align: center;
  font-size: 13px;
  font-weight: bold;
  font-family: 'Lato', sans-serif;
  color: #101820;
}

.whiteBox {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 3px 6px #10182233;
  width: 100vw;
  justify-content: center;
  align-items: center;
  height: 22vw;
  background: white;
  max-width: 41vw;
  max-height: 180px;
}

.whiteBox > p {
  color: #007cbb;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  font-family: 'Lato', sans-serif;
}

.greenArrow {
  width: 20px;
  z-index: -1;
}

.redArrow {
  width: 20px;
  z-index: -1;
}

@media (min-width: 768px) {
  .cardWrapper {
    display: flex;
    width: 40%;
    max-width: 350px;
    max-height: 180px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 13px;
    margin-top: 30px;
  }

  .cardWrapper > h3 {
    text-align: center;
    margin: 0;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    font-family: 'Lato', sans-serif;
    color: #101820;
  }

  .whiteBox {
    display: flex;
    flex-direction: row;
    box-shadow: 0px 3px 6px #10182233;
    width: 18vw;
    justify-content: center;
    align-items: center;
    height: 22vw;
    background: white;
    max-width: 41vw;
    max-height: 180px;
    gap: 10px;
  }
}
